import uniBadge from '@/components/uni-badge/uni-badge.vue';
export default {
	components: {
		uniBadge
	},
	data() {
		return {
			listData: [],
			user: {},
			token: '',
			index:0,
			selectData:["未读","所有"]
		};
	},
	onLoad(e) {
		this.user = JSON.parse(e.list);
		this.token = e.token;
	},
	onShow() {
		let url = 'manageUserUploads/selectHabit.json';
		const data = {
			teacherId: this.user.id,
			token: this.token
		};
		this.uniHttp.getJSON(url, data, res => {
			if (res.data.success) {
				this.listData = res.data.data;
			}
		});
	},
	methods: {
		tabList(list) {
			let url = 'manageUserUploads/selectHabitClock.json';
			const data = {
				typeId: list.id,
				teacherId: this.user.id,
				token: this.token
			};
			this.uniHttp.getJSON(url, data, res => {
				if (res.data.success) {
					uni.navigateTo({
						url: '../peiyou1/peiyou1?data=' + JSON.stringify(res.data.data) + '&user=' + JSON.stringify(this.user) + '&token=' + this.token
					});
				}
			});
		},
		bindPickerChange(){
			
		}
	}
};
